
body{
    background: url(../images/forest.jpg) no-repeat center fixed;
}

.style-card{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    grid-gap: 0 10px;
    grid-auto-rows: 10px;
}

.style-card img{
    width: 250px;
    grid-row-end: span 2;
}



